<template>
  <router-link :to="`/resident/${id}`" class="residents-card">
    <div class="residents-card__img">
      <img :src="user.avatar.url" alt="photo" width="260" height="150" />
    </div>
    <div class="residents-card__wrap">
      <div class="residents-card__name">
        {{ user.full_name }}
      </div>
      <div class="residents-card__rating">
        <star-rating
          :star-size="20"
          :rating="user.rating"
          :read-only="true"
        ></star-rating>
        <!--                <div class="residents-card__cup">-->
        <!--                  <svg class="icon">-->
        <!--                    <use href="@/assets/img/sprite.svg#cup-1"></use>-->
        <!--                  </svg>-->
        <!--                </div>-->
      </div>
      <div class="residents-card__footer">
        <div class="residents-card__count-startup">
          <span>{{ user.startupsCount }}</span>
          {{ getNoun(user.startupsCount, "стартап", "стартапа", "стартапов") }}
        </div>
        <div class="residents-card__count-event">
          <span>{{ user.eventsCount }}</span>
          {{ getNoun(user.eventsCount, "событие", "события", "событий") }}
        </div>
      </div>
    </div>
  </router-link>
</template>

<script>
import StarRating from "vue-star-rating";
import mixin from "../../mixin/mixin";

export default {
  name: "ResidentCard",
  mixins: [mixin],
  components: { StarRating },
  props: {
    user: {
      type: Object,
      default: () => {}
    },
    id: {
      type: Number,
      default: null
    }
  }
};
</script>

